.achievements {
    //max-height: 600px;
    width: 100%;
    .divider-heading {
        height: 2px;
        width: 50px;
        background: #eb2f64;
    }
    &__note {
        margin-top: 1em;
    }
    &__list {
        margin-top: 2em;
        display: grid;
        grid-template-rows:repeat(3, max-content);
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        gap: 30px;
    }
}